.entry-content .wp-block-tasty-block-container {
  padding: 30px 50px;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06); }

.wp-block-tastydigital-splash-box {
  position: relative; }
  .wp-block-tastydigital-splash-box > a {
    display: block;
    overflow: hidden; }
    .wp-block-tastydigital-splash-box > a img.splash-image {
      transform: scale(1);
      transform-origin: 50% 50%;
      transition: transform ease-in-out 0.2s;
      z-index: 0; }
    .wp-block-tastydigital-splash-box > a:hover img.splash-image {
      transform: scale(1.05); }
  .wp-block-tastydigital-splash-box .overlay {
    position: absolute;
    bottom: 0;
    width: 100%;
    text-align: center;
    padding: 20px;
    height: 88px;
    z-index: 2; }
  .wp-block-tastydigital-splash-box h2 {
    margin: 5px 0 0;
    color: #FFF;
    font-weight: 700; }

.opacity-90 {
  opacity: 0.9;
  z-index: 1; }

.splash-image {
  text-align: center;
  background-color: #F5F5F5;
  min-width: 100%; }

@media only screen and (min-width: 960px) {
  .wp-block-group.constrain-content > .wp-block-group__inner-container {
    max-width: 1062px;
    margin-left: auto;
    margin-right: auto; } }

@media screen and (max-width: 768px) {
  .mobile-hidden {
    display: none; } }

.is-style-flexible-row .wp-block-group__inner-container {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  align-items: center; }
  .is-style-flexible-row .wp-block-group__inner-container > * {
    padding-left: 0.6em;
    padding-right: 0.6em; }
